<template>
  <h3 class ="text-danger text-center my-3">THNG K S LIU </h3>
    <div class="d-flex flex-row text-center">
         <div class="col-12 "> 
             <div class=" d-flex flex-row justify-content-between ms-5 mb-3" >
              <div class=" fs-4 " ><span class="text-primary fw-bold">Tng s tour:</span> {{ props.totalTour }}</div>
              <div class="fs-4"><span class="text-primary fw-bold">Tng s khch sn:</span> {{ props.totalHotel }}</div>
              <!-- <div class=" fs-4"><span class="text-primary fw-bold">Tng s nhn vin:</span> {{ props.account}}</div> -->
              <div class=" fs-4"><span class="text-primary fw-bold">Tng s khch hng:</span> {{ props.totalcustomer}}</div>
             </div>
          <table
                    class="table text-center  table-hover  "
                >
                    <thead class=" table-active table-primary">
                      <tr>
                        <th scope="col" v-for="month in 12" :key="month">Thng {{ month }}</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                          <td v-for="item in revenuesThisYear">{{ formatCurrency(item) }}</td>
                       </tr>
                     </tbody>
                  </table>
            </div>
    </div>
    <h4 class ="text-danger text-center  my-3">Doanh s t tour so vi cung k nm ngoi</h4>
    <div class="row ">
       <div class="d-flex flex-row">
        <div class="line-chart-container col-8">
          <Line :data="lineChartData" :options="chartOptions" />
       </div>
       <div class="pie-chart-container my-4 col-4 ">
          <Pie :data="pieChartData" :options="PiechartOptions" />
       </div>
       </div>
    </div>
     
  
 
  </template>
  
  <script setup>
  import API from '../../service/Base/api';
  import { ref } from 'vue';
  import { Line, Pie } from 'vue-chartjs';
  import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  ArcElement, // Thm ArcElement
  PieController ,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement  // Thm PieController
} from 'chart.js';
import Tour_constants from '~/assets/js/constants/constants';
import { formatCurrency } from '~/assets/js/validate';
import { useRouter } from 'vue-router';
const props = defineProps({
  totalBill: Number,
  totalTour: Number,
  totalHotel: Number,
  account: Number,
  totalB: Number,
  totalcustomer: Number,
})
// ng k cc thnh phn vi Chart.js
ChartJS.register(
  Title,
  Tooltip,
  Legend,
  ArcElement,
  PieController,
  CategoryScale,
  LinearScale,
  PointElement ,
  LineElement 
);
const api = new API();
const router = useRouter();
const revenuesThisYear = ref([]);
const isLoading = ref(false);
definePageMeta({
    middleware: ['auth','admin','auth-admin'],
});
  const lineChartData = ref({
    labels:[],
    datasets: [
    {
      label: 'Nm nay',
      backgroundColor: 'rgba(75, 192, 192, 0.2)',
      borderColor: '#33691E',
      data: [] 
    },
    {
      label: 'Nm ngoi',
      backgroundColor: 'rgba(255, 99, 132, 0.2)',
      borderColor: '#D32F2F',
      data: [] 
    }
  ]
  });
  const pieChartData = ref({
  labels: [],
  datasets: [
    {
      label: 'Doanh thu',
      backgroundColor: ['#33691E', '#D32F2F'], // Mu nht hn vi  trong sut l 0.2
      borderColor: 'transparent', // Xa nn k
      borderWidth: 0,
      data: []
    }
  ]
});
const out = () => {
  router.push('/login');}
  
  const chartOptions = ref({
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    legend: {
      position: 'top'
    },
    title: {
      display: true,
      text: 'Doanh thu theo tng thng',
    
    }
  },
  scales: {
    x: {
      title: {
        display: true,
        text: 'Thng'
      }
    },
    y: {
      title: {
        display: true,
        text: 'Doanh thu (VND)'
      },
      beginAtZero: true
    }
  },
  elements: {
    line: {
      tension: 0.4 // iu chnh  mm mi ca ng cong, gi tr t 0 n 1, mc nh l 0.2
    }
  },
  // t kiu u ca ng cong
  interaction: {
    intersect: false,
  },
  plugins: {
    tooltip: {
      intersect: false,
    }
  },
});
const PiechartOptions = ref({
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    legend: {
      position: 'top'
    },
    title: {
      display: true,
      text: 'Phn tch t l n t',
      font: {
        size: '20px' // t kch thc font di dng chui vi n v
      }
    },
    tooltip: {
      callbacks: {
        label: function(context) {
          let label = context.label || '';
          if (label) {
            label += ': ';
          }
          label += context.formattedValue + '%';
          return label;
        }
      }
    },
   
  }
});


  // Mocked data function (acts as an internal API)
  const fetchMockedRevenueData = async () => {
  try {
    isLoading.value = true;
    // Gi API  ly d liu revenues t pha backend cho nm nay
    const responseThisYear = await api.get(`/Booking/ToTalBillEachMonth?year=${new Date().getFullYear()}`, null);
    revenuesThisYear.value = responseThisYear.data.responseData;

    // Gi API  ly d liu revenues t pha backend cho nm trc
    const responseLastYear = await api.get(`/Booking/ToTalBillEachMonth?year=${new Date().getFullYear() - 1}`, null);
    const revenuesLastYear = responseLastYear.data.responseData;

    // Tr v d liu c cu trc labels v revenues cho c hai nm
    return {
      labels: ['Thng 1', 'Thng 2', 'Thng 3', 'Thng 4', 'Thng 5', 'Thng 6', 'Thng 7', 'Thng 8', 'Thng 9', 'Thng 10', 'Thng 11', 'Thng 12'],
      revenuesThisYear: revenuesThisYear,
      revenuesLastYear: revenuesLastYear
    };
  } catch (error) {
    console.error('Error fetching mocked revenue data:', error);
    throw error;
  }
};
const countBookingCanelorNot = async () => {
  try {
    // Gi API  ly s lng n t hng thnh cng v b hy
    const responseCancel = await api.get(`/Booking/CountBookingCancelorNotCancel?status=${Tour_constants.Cancel}`);
    const Cancel = responseCancel.data.responseData;

    const responseSuccess = await api.get(`/Booking/CountBookingCancelorNotCancel?status=${Tour_constants.Paid}`);
    const Success = responseSuccess.data.responseData;
    const responsecustomercancel = await api.get(`/Booking/CountBookingCancelorNotCancel?status=${Tour_constants.Customercancel}`);
    const Customercancel = responsecustomercancel.data.responseData;
    // Tr v d liu
    return {
      Cancel: Cancel,
      Success: Success,
      Customercancel : Customercancel
    };
  } catch (error) {
    console.error('Error fetching booking count data:', error);
    throw error;
  }
};
  // Fetch data from the mocked API
  const fetchRevenueData = async () => {
    try {
      const data = await fetchMockedRevenueData();
      lineChartData.value.labels = data.labels;
    lineChartData.value.datasets[0].data = data.revenuesThisYear;
    lineChartData.value.datasets[1].data = data.revenuesLastYear;
    
      console.log(lineChartData.value)
      const dataCount = await countBookingCanelorNot();
    
    // Cp nht d liu cho biu  hnh trn
    const totalOrders = dataCount.Success + dataCount.Cancel +dataCount.Customercancel;
    const successPercentage = (dataCount.Success / totalOrders) * 100;
    const cancelPercentage = ((dataCount.Cancel + dataCount.Customercancel) / totalOrders) * 100;

    // Cp nht d liu cho biu  hnh trn
    pieChartData.value.labels = ['n thnh cng', 'n b hy'];
    pieChartData.value.datasets[0].data = [successPercentage, cancelPercentage];
    console.log(pieChartData.value)
    } catch (error) {
      console.error('Error fetching revenue data:', error);
    }
    finally {
    isLoading.value = false; // Set loading state to false regardless of success or failure
  }
  } 
  ;
  await fetchRevenueData(); 
  </script>
  <style scoped>
  .line-chart-container
  {
    position: relative;
    height: 400px;
    width: 76%; /* Set the width to control the size of the pie chart */
  }
  .pie-chart-container
  {
    position: relative;
    height: 250px;
    width: 300px; 
  }
  </style>
  